@import './summernote.css';

:root {
  --modal: 1045;
}

.note-editor .note-dropdown-item h1 {
  display: block;
  /* h1의 크롬 브라우저 초기값 세팅*/
  /* stylelint-disable-next-line declaration-property-unit-disallowed-list */
  font-size: 2em;
  margin-block-start: 0.67em;
  margin-block-end: 0.67em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}
.note-editor {
  background-color: var(--white);
  max-width: 1200px;
}
.note-editor.note-frame.fullscreen {
  max-width: 100%;
}

.note-editor .note-editor {
  font-size: 14px;
  font-family: 'Noto Sans KR', 'Roboto', sans-serif;
}
.note-editor .note-editor:not(font) {
  all: revert;
}
.note-editor .note-editor h1 {
  display: block;
  /* stylelint-disable-next-line declaration-property-unit-disallowed-list */
  font-size: 2em;
  margin-block-start: 0.67em;
  margin-block-end: 0.67em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}

.note-modal {
  z-index: var(--modal);
}

.note-modal-header {
  position: relative;
}

.note-modal-header .close {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 0;
  width: 30px;
  height: 30px;
  background-color: transparent;
  cursor: pointer;
  border: 0;
  font-size: 16px;
}

.note-modal-footer,
.note-dropdown-menu {
  box-sizing: content-box;
}
